<template>
  <div class="main">
    <div class="main-table" v-cloak>
      <div style="border:1px solid #ccc">
        <ul>
          <li style="width:10%">
            <el-select v-model='search.proj' @change="changeSelect('proj')">
                <template v-for="(item, key) in teamData" :value='key'>
                  <el-option :label="item.memo" :value="item.id" :key="key">{{item.memo}}</el-option>
              </template>
            </el-select>
          </li>
          <li style="width:13%">
            <el-select v-model='search.corp' @change="changeSelect">
               <template v-for="(item, key) in pmemoList" :value="key">
                  <el-option :label="item.pmemo" :value="item.prjid_local" :key="key">{{item.pmemo}}</el-option>
              </template>
            </el-select>
          </li>
          <li style="width:57%">
            <ol>
              <li>
                模型名称
              </li>
              <li>历史成功率</li>
              <li>累计外呼</li>
              <li>权重</li>
            </ol>
          </li>
          <li style="width:15%;margin:10px;text-align:right">
            <el-button class="btn-small" v-show="del.allState" @click="batchRemove">批量删除</el-button>
            <el-button class="btn-small" v-show="del.state" @click="cancel">取消</el-button>
            <el-button class="btn-small" style="background-color: #007bc4;color: #fff;" v-show="del.state" @click="delArr">删除</el-button>
          </li>
        </ul>
        <ul v-for="(item, index) in list.data">
          <li style="width:10%;height:40px;line-height:40px;">
            <el-checkbox name="proj" v-show="del.state" v-model="item.prjidlocalid" @change="checked=>changeprjidlocal(checked,item,index)"></el-checkbox>
            <span v-if="teamData2[item.cmemo] != undefined">{{teamData2[item.cmemo]}}</span>
          </li>
          <li style="width:13%;height:40px;line-height:40px;">
            <el-checkbox name="projtype" v-show="del.state" v-model='item.prjidccid' @change="checked=>changeprjidcc(checked,item,index)"></el-checkbox>
            <span>{{pmemoData[item.prjid_cc]}}</span>
          </li>
          <li style="width:57%">
            <ol v-for="model in item.mdata">
              <li>
                <p><el-checkbox name="model" v-show="del.state" v-model="model.prjidmid"  @change="checked=>changemid(checked,item,model,index)"></el-checkbox>
                  <span style="cursor:pointer" :title='modelData2[model.mid]' class="model-span">{{modelData2[model.mid]}}</span></p>
              </li>
              <li>{{model.leads_rate}}%</li>
              <li>{{model.hcallout}}</li>
              <li><el-input v-model="model.rank"></el-input></li>
            </ol>
            <!-- <draggable class="list-group" tag="div" v-model="index.modellist" v-bind="dragOptions" :options="{sort:index.adjust_type,group:index.modellist}" :move="getdata" @start="isDragging=true" @end="isDragging=false">
              <transition-group type="transition">

              </transition-group>
            </draggable> -->
          </li>
          <li style="width:16%;margin:10px 0;text-align:right">
            <!-- <el-button class="btn-small" value="" @click="adjust(item)">调整模型顺序</el-button> -->
            <el-button class="btn-small" value="" @click="addproj(item)">添加模型</el-button>
            <el-button class="btn-small" value="" @click="editrank(item)">保存</el-button>
          </li>
        </ul>
      </div>
      <page-table show_size="true" v-bind:cur="search.page" v-bind:total_page="list.totalPage" v-bind:total_count="list.totalCount" @change="change_data"></page-table>
    </div>
    <el-dialog :visible.sync="dialog_add" title="请从下拉菜单中选择新增模型">
      <div>
        <ul class="dialog-ul">
          <li style="width: 395px; margin-left: 5px">模型名称</li>
          <li style="margin-left: 3px;">历史成功率</li>
          <li style="margin-left: 10px">累计外呼</li>
        </ul>
        <ol v-for="(item, index) in addlist">
          <li>
            <el-select class="dialog_projselect" style="width:100%" v-model='item.mid' @change='changemodel( item.mid, index)'>
              <template v-for="(model, index) in addmodellist" :value='index'>
                <el-option :label="model.memo" :value="model.id"  :key="model.id">{{model.memo}}</el-option>
              </template>
            </el-select>
          </li>
          <li>{{item.leads_rate}}%</li>
          <li>{{item.hcallout}}</li>
          <li style="cursor:pointer" v-if='addlist.length != "1"' @click='dellist(index)'>删除</li>
        </ol>
      </div>
      <el-button @click="addprojselect()">添加模型</el-button>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialog_add=false">取消</el-button>
        <el-button type="primary" @click="save">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import pageTable from '@/components/pager'
import ajax from "../../config/ajax"
import {format} from '@/config/util'
import draggable from 'vuedraggable'
import Vue from 'vue'
import Qs from 'qs'
export default {
  name: 'data-alignment',
  components:{pageTable},
  data () {
    return {
      search:{
        corp: '',
        proj: '',
        model: '',
        size: '20',
        page: '1'
      },
      list:{
        data:[],
        totalCount:'',
        totalPage: '',
      },
      del:{               //批量删除相关参数
        datas:{
          prijid_local: [],
          prijid_cc: [],
          prjid_mid: []
        },
        state:false,
        allState:true
      },
      prjid_local: '',       //新增品牌id
      prjid_cc: '',
      teamData: [],          //外呼团队
      teamData2: [],
      corpData:[],
      projData: [],
      pmemoData: [],         //外呼团队数组字典
      pmemoList: [],      //
      modelData: [],         //模型数组字典
      modelData2: [],       
      addmodellist: [],       //新增品牌模型数组
      addlist: [],          //新增品牌数组
      dialog_add:false,     //新增品牌弹框
      isDragging: false,     //是否拖拽
      delayedDragging: false,
      delarr: [],
      checklist: {
        prjid_localid: false,
        prjid_ccid: false
      }
    }
  },
  created() {
    this.teamData = JSON.parse(localStorage.getItem("cropid"));
    this.teamData2 = JSON.parse(localStorage.getItem("brand"));
    this.projData = JSON.parse(localStorage.getItem("pro"));
    this.pmemoData = JSON.parse(localStorage.getItem("pmemo"));
    this.modelData = JSON.parse(localStorage.getItem("model"));
    this.modelData2 = JSON.parse(localStorage.getItem("curl"));
    this.search.model = this.modelData[0].id;
    this.search.corp = '0';
    this.search.proj = this.teamData[0].id;
    this.getInitialList();
    this.getProjList();
  },
  methods: {
    getInitialList() {
      this.del.state=false
      this.del.allState=true
      this.delarr=[]
      var param = {
        cid: this.search.proj,
        prjid_local: this.search.corp,
        size: this.search.size,
        page: this.search.page
      }
      return ajax.request({
        method: 'get',
        url: 'rate/all',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        params: param
      }).then(res => {
        // console.log(res.data.list)
        this.list.data = res.data.list;
        this.list.totalCount = res.data.totalCount;
        this.list.totalPage = res.data.totalPage;
        for(let i=0;i<this.list.data.length;i++){
          Vue.set(this.list.data[i],'prjidlocalid',false);
          Vue.set(this.list.data[i],'prjidccid',false);
          for(let j=0;j<this.list.data[i].mdata.length;j++){
            Vue.set(this.list.data[i].mdata[j],'prjidmid',false);
          }
        }
      }).catch(error => {
        console.log(error)
      })
    },
    getProjList() {
      var params = {
        cid: this.search.proj
      }
      return ajax.request({
        method: 'get',
        url: 'pmemo/cid',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        params: params
      }).then((res) => {
        // console.log(res.data.list)
        var defaultData = {
          prjid_local: 0,
          pmemo: '全部'
        }
        res.data.list.unshift(defaultData);
        this.pmemoList = res.data.list;
        this.search.corp = this.pmemoList[0].prjid_local;
        this.getInitialList();
      });
    },
    changeSelect(type) {     //下拉框重新获取数据
      if (type == 'proj') {
        // console.log(this.search.proj)
        this.getProjList();
      } else{
        this.getInitialList();
      }
    },
    adjust(obj){        //调整模型顺序
      if(obj.adjust_type){
        obj.adjust_type = false;
      }else{
        obj.adjust_type = true;
      }

    },
    //拖拽
    getdata({ relatedContext, draggedContext }){
      const relatedElement = relatedContext.element;
      const draggedElement = draggedContext.element;
    },

    //批量删除
    changeprjidlocal(checked,item){
      for(let i=0;i<this.list.data.length;i++){
        var obj = {};
        if(this.list.data[i].prjid_local==item.prjid_local){
          Vue.set(this.list.data[i],'prjidlocalid',checked);
          Vue.set(this.list.data[i],'prjidccid',checked);
          obj.prjid_local = this.list.data[i].prjid_local;
          obj.prjid_cc = this.list.data[i].prjid_cc;
          obj.mid = "";
          for(let j=0;j<this.list.data[i].mdata.length;j++){
            if(!checked){
              this.delarr.splice(i,1);
              // console.log(this.delarr)
              Vue.set(this.list.data[i].mdata[j],'prjidmid',false);
            }else{
              Vue.set(this.list.data[i].mdata[j],'prjidmid',checked);
              obj.mid += this.list.data[i].mdata[j].mid+",";
              this.delarr[i] = obj; 
            }
          }
          var reg=/,$/gi;
          obj.mid=obj.mid.replace(reg,"");
          // console.log(this.delarr);
        }
      }
    },
    changeprjidcc(checked,item,index){
      let obj = {};
      for(let i=0;i<this.list.data.length;i++){
        if(this.list.data[i].prjid_local==item.prjid_local){
          if(!checked){
            this.delarr.splice(i,1);
            // console.log(this.delarr);
            Vue.set(this.list.data[i],'prjidlocalid',false);
            Vue.set(this.list.data[i],'prjidccid',false);
            for(let j=0;j<this.list.data[i].mdata.length;j++){
              Vue.set(this.list.data[i].mdata[j],'prjidmid',false);
            }
          }else{
            obj.prjid_local = item.prjid_local;
            obj.prjid_cc = item.prjid_cc;
            obj.mid = "";
            Vue.set(item,'prjidccid',checked);
            for(let i=0;i<item.mdata.length;i++){
              obj.mid += item.mdata[i].mid+",";
              Vue.set(item.mdata[i],'prjidmid',checked);
            }
            var reg=/,$/gi;
            obj.mid=obj.mid.replace(reg,""); 
            this.delarr[index] = obj;
          }
        }
      }
    },
    changemid(checked,item,model,index){
      for(let i=0;i<this.list.data.length;i++){
        if(this.list.data[i].prjid_cc==item.prjid_cc){
          if(!checked){
            Vue.set(this.list.data[i],'prjidlocalid',false);
            Vue.set(this.list.data[i],'prjidccid',false);
            let midarr = this.delarr[i].mid.split(",");
            for(let k=0;k<midarr.length;k++){
              if(midarr[k] == model.mid){
                midarr.splice(k,1);
              }
            }
            if(midarr.length == '0'){
              this.delarr.splice(i,1);
            }else{
              this.delarr[i].mid = midarr.join(",");
            }
          }else{
            if(this.delarr[i] == undefined){
              var obj ={};
              obj.prjid_local = item.prjid_local;
              obj.prjid_cc = item.prjid_cc;
              obj.mid = model.mid+",";
              var reg=/,$/gi;
              obj.mid=obj.mid.replace(reg,"");
              this.delarr[index] = obj;
            }else{
              this.delarr[i].mid+=","+model.mid;
            }
            if(this.delarr[i].mid.split(",").length == this.list.data[i].mdata.length){
              Vue.set(this.list.data[i],'prjidccid',checked);
            }
            // Vue.set(model,'prjidmid',checked);
          }
        }
      }
    },
    batchRemove(){
      this.del.state=true
      this.del.allState=false
    },
    cancel(){
      this.del.state=false
      this.del.allState=true
    },
    //删除
    delArr() {
      if (this.delarr.length <=0){
        this.$message.info("请选择您要删除的文件");
      }else{
        return ajax.request({
          method: 'post',
          url: 'rate/delete',
          data: {
            mdata:this.delarr
          }
        }).then(res => {
          if(res.code=='200'){
            this.$message.success("删除成功");
            this.getInitialList();
          }
          // console.log(res)
        }).catch(err => {
          console.log(err)
        })
      }
    },
    
    //修改权重
    editrank(list){
      let mdata = [];
      for(let i in list.mdata){
        let data = {};
        data.mid = list.mdata[i].mid,
        data.rank = list.mdata[i].rank
        mdata.push(data);
      }
      let params={
        prjid_cc: list.prjid_cc,
        prjid_local: list.prjid_local,
        mdata: mdata
      }
      return ajax.request({
        method: 'post',
        url: 'rate/update',
        data: params
      }).then(res => {
        if(res.code == '200'){
          this.$message.success("保存成功");
          this.getInitialList();
        }else{
          this.$message.error("保存失败");
        }
      }).catch(err => {
        console.log(err);
      })
    },
    addproj(list){        //添加模型弹框
      // console.log(list)
      this.dialog_add = true;
      this.prjid_local = list.prjid_local;
      this.prjid_cc = list.prjid_cc;
      this.addlist = [];
      this.getmodel(list);
    },
    //获取模型数据
    getmodel(){
      return ajax.request({
        method: 'get',
        url: 'area',
        params: {
          prjid_local: this.prjid_local
        }
      }).then(res => {
        // console.log(res.data.model)
        if(res.code == '200'){
          if(res.data.model.length != 0){
            this.addmodellist = res.data.model;
            this.matchModel(res.data.model);
          }else{
            this.modelData.shift();
            this.addmodellist = this.modelData
          }
          var data = {
            mid: this.addmodellist[0].id,
            leads_rate: '',
            hcallout: ''
          }
          this.addlist.push(data);
          this.getcount(this.prjid_local, this.addmodellist[0].id, this.addlist.length-1);
        }
      }).catch(error => {
        console.log(error)
      })
      
    },
    //匹配模型
    matchModel(model){
      // console.log(model[0]);
      // console.log(this.modelData)
      this.addmodellist=[]
      for (var i=0;i<model.length;i++){
        Object.keys(this.modelData2).map((key,item)=>{
          if (model[i]  == key){
            this.addmodellist.push({
              'memo':this.modelData2[key],
              'id':model[i]
            })
          }
        });
      }
      // console.log(this.addmodellist);
    },
    //获取历史成功率及历史外呼数
    getcount(prjid_local, mid, index){
      return ajax.request({
        method: 'get',
        url: 'count',
        params: {
          prjid_local: prjid_local,
          mid: mid
        }
      }).then(res => {
        if(res.code == '200'){
          if(res.data[0] != 'undefined'){
            this.addlist[index].leads_rate = res.data[0].hleadsrate;
            this.addlist[index].hcallout = res.data[0].hcallout;
          }else{
            this.addlist[index].leads_rate = '';
            this.addlist[index].hcallout = '';
          }
        }
        
      }).catch(err => {
        console.log(err)
      })
    },
    //选择模型下拉框重新获取历史成功率
    changemodel( mid, index){
      this.getcount(this.prjid_local, mid, index)
    },
    addprojselect(){
      var data = {
        mid: this.addmodellist[0].id,
        leads_rate: '',
        hcallout: ''
      }
      this.addlist.push(data);
      this.getcount(this.prjid_local, this.addmodellist[0].id, this.addlist.length-1);
    },
    
    dellist(index){
      this.addlist.splice(index,1);
    },
    save(){
      let mid = [];
      for(let i in this.addlist){
        mid.push(this.addlist[i].mid);
      }
      // console.log(this.prjid_cc);
      return ajax.request({
        method: 'post',
        url: 'rate/add',
        data:{
          prjid_cc: this.prjid_cc,
          prjid_local: this.prjid_local,
          mid: mid.join(",")
        }
      }).then(res => {
        if(res.code == '200'){
          this.$message.success("添加成功");
          this.dialog_add = false;
          this.getInitialList();
        }else{
          this.$message.error("添加失败");
        }
      }).catch(err => {
        console.log(err)
      })
    },
    change_data (index, size) {
      this.search.page = index;
      this.search.size = size;
      this.del.state=false
      this.del.allState=true
      this.delarr=[]
      this.getInitialList()
    }
  },
  computed:{
    dragOptions() {
      return {
        animation: 500,
        group: "description",
        ghostClass: "ghost"
      };
    },
  },
  watch: {
    isDragging(newValue) {
      if (newValue) {
        this.delayedDragging = true;
        return;
      }
      this.$nextTick(() => {
        this.delayedDragging = false;
      });
    }
  }
}
</script>

<style scoped>
  @import '../../assets/css/pages/main.css';
  [v-cloak]{
      display: none
  }
  .main-table{
    padding:15px!important;
  }
  .main .main-table ul{
    width:100%;
    overflow:hidden;
    border-bottom:1px solid #ccc;
  }
  .main .main-table ul:last-child{
    border-bottom:none;
  }
  .main .main-table ul li{
    float:left;
    padding:8px 0;
    margin-left:10px;
  }
  .main .main-table ul li .el-select .el-input{
    width:110px;
  }
  .main .main-table ol{
    overflow:hidden;
    margin-bottom:8px;
  }
  .main .main-table ol li{
    padding:0!important;
    margin-left: 0px;
  }
  .main .main-table ol li p{
    height:30px;
    line-height:30px;
    margin:5px 0;
  }
  .main .main-table ol li.current p{
    border:1px solid #ccc;
  }
  .main .main-table ol li{
    line-height:40px;
  }
  .main .main-table ol li:first-child{
    width:380px;
    overflow:hidden;
  }
  .main .main-table ol li:nth-child(2){
    width:70px;
    text-align:center
  }
  .main .main-table ol li:nth-child(3){
    width:60px;
    text-align:center
  }
  .main .main-table ol li:nth-child(4){
    width:45px;
    text-align:center
  }
  .main .main-table ol li:first-child .el-select .el-input{
    width:260px;
  }

  .main .main-table ol li{
    margin-right:10px;
  }
  .model-span{
    display: inline-block;
    width: 340px;
  }
  .el-checkbox{
    margin-right:8px;
  }
  .el-select-dropdown__item{
    padding:0 20px;
  }
  .el-dialog ol{
    overflow:hidden;
  }
  .el-dialog ol li{
    float:left;
    line-height:40px;
    margin-right:10px;
    margin-bottom:8px;
  }
  .el-dialog ol li:first-child{
    width:55%;
    padding:0;
    border:none;
  }
  .el-dialog ol li {
    width: 65px;
    height: 42px;
    border-radius:5px;
    border:1px solid #ccc;
    text-align:center;
  }
  .btn-small{
    min-width:0!important;
  }
  .flip-list-move {
    transition: transform 0.5s;
  }

  .no-move {
    transition: transform 0s;
  }

  .ghost {
    opacity: 0.5;
    background: #c8ebfb;
  }

  .list-group {
    min-height: 20px;
  }

  .list-group-item {
    cursor: move;
  }

  .list-group-item i {
    cursor: pointer;
  }
  .dialog-ul li {
    display: inline-block;
    margin-bottom: 10px;
  }
</style>



